.all-pokes{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
  }
  
  .poke-card{
    background-color: #fce256;
    width: 280px;
    height: 380px;
    border-radius: 5px;
    box-shadow: 2px 2px 10px black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    padding-top: 10px;
    margin: 10px;
    position: relative;
  }
  
  .poke-card:hover{
    box-shadow: 0px 0px 10px rgb(215, 248, 211);
    transform: scale();
  }

  .poke-card-content{
    background-color: green;
    width: 260px;
    height: 375px;
    border-radius: 5px;
    box-shadow: -3px -3px 3px rgb(1, 78, 1);
    position: relative;

    background-image: linear-gradient(to right bottom, #fce256, green,#fce256,green, #fce256);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    /*delete this font*/
    font-size: 8px;
  }

.top-info{
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    justify-content: space-between;
}

.rarety{
    background-color: aliceblue;
    color: black;
    box-shadow: 2px 2px 2px black;
    border-radius: 5px;
    font-size: 10px;
    padding: 0 10px;
    position: relative;
    left: -5px;
    background-image: linear-gradient(to right bottom, rgb(250, 250, 250), rgb(163, 161, 161),rgb(250, 250, 250),rgb(150, 149, 149), rgb(250, 250, 250));
    text-shadow: 0 0 5px white; 
    font-weight: 500;
    font-style: italic;
}
.poke-name{
    color: black;
    font-size: 20px;
    
}

.hpKey{
    color: black;
    font-size: 8px;
    font-weight: bold;
    display: inline;
}

.hpValue{
    color: black;
    font-size: 20px;
    font-weight: bold;
    display: inline;
}

.types{
    display: flex;
}

.image-frame{
    background-color: aliceblue;
    width: 90%;
    border-radius: 2px;
    border: rgb(254, 254, 254) solid;
    box-shadow: inset 2px 2px 2px black,  2px 2px 2px black;
}

.image-frame-bottom{
    width: 90%;
    border-radius: 2px;
    border: rgb(254, 254, 254) solid;
    box-shadow: inset 2px 2px 2px black,  2px 2px 2px black;
    text-align: center;
    color: black;
    text-shadow: 0 0 5px white;
    background-image: linear-gradient(to right bottom, rgb(250, 250, 250), rgb(163, 161, 161),rgb(250, 250, 250),rgb(150, 149, 149), rgb(250, 250, 250));
}

.type-image{
    object-fit: cover;
    height: 20px;
    width: 20px;
}
  .poke-image{
    background-color: rgb(252, 251, 251);
    width: 100%;
    height: 150px;
    margin: 2px;
  }
.abilities{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.ability{
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-weight: bold;
    color: black;
    text-shadow: 0 0 5px white;
}
  .single-poke{
    color: rgb(119, 80, 2);
  }